			<h3 class="conTitle">{{item.tag}}</h3>
			<p class="conText">{{item.desc}}</p>
		</section>

	</div> 
</template>

<script>
import subHeader from '../header/SubHeader'
export default {
	name: 'classify',
  	data () {
	    return {
	    	subTitle:'',
	    	conList:[
	    		{
	    			tag:"",
	    			desc:"  ",
	    		},
	    		{
	    			tag:"",
	    			desc:"  ",
	    		}  
	    	]
	    }
  	},
  	components:{
  		"app-subHeader":subHeader
  	}
}
</script>
 


