		text-decoration: underline;
	}
	.mask{
        width:100%;
        height: 100%;
        background:rgba(0,0,0,.3);
        position: fixed;
        top:0;
        left: 0;
        
        z-index: 1000;
    }
    .content{
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
        border-radius: 7px;
        background: #fff;
        width:6.5rem;
        height: 8rem;
    }  
    .content .topTitles{
        width: 100%;
        height: .7rem;
        line-height: .7rem;
        text-align: center;
        color:#fff;
        font-size: .32rem;
        background-color: #ff5555;
        border-radius:3px 0px 0px;
         margin-bottom:.3rem;
    }
    .content .scroll{
    	width: 100%;
    	height: 6.6rem;
    	overflow-y: scroll;
    }
    .content .close{
        width: .35rem;
        height: .35rem;
        line-height: .32rem;
        color:#fff;
        font-size: .26rem;
        text-align: center;
        position: absolute;
        top:.08rem;
        right: .08rem;
        border: solid 1px #ffffff;
        border-radius: 30px;
        cursor: pointer;
    }
    
    .content p{
        font-size: .26rem;
        text-align: left;
	    line-height: .4rem;
	    color: #333;
	    width: 92%;
	    margin: .08rem auto;
    }
    .content .incent{
