<template>
	<div class="invest">
		<app-subHeader :subTitle="subTitle"></app-subHeader>
		<div class="container">
		 	<p class="conTitle"></p>
		 	<p class="c3">/</p>
		 	<p class="red">=(-) x  x - - </p>
		 	<p class="c3 mt25">/</p>
		 	<p class="red">=(-) x  x /<br><i class="w1"></i>  -  - </p>
            <p class="conTitle mt40"></p>
		 	<p class="conText">      12822002,0001,3101,3302 </p>
		 	<p class="c3">=<span class="red"> $2,000</span> </p>
		 	<p class="c3">=<span class="red"> $(1,330 - 1,310) x 100 x 2- 0 - 0 </span> <br><i class="w175"></i>  = <span class="red">$4000 </span></p>
		</div>
	</div> 
</template>

<script> 
import subHeader from '../header/SubHeader'  
export default {
	name: 'invest', 
	 data () {
	    return {
	    	subTitle:'',
	    	 
	    }
  	},
  	components:{ 
  		"app-subHeader":subHeader,
  	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>  
	.container{
		padding:.3rem;
	}
	.w1{
		display:inline-block;
		width:1rem;
	}
	.w175{
		display:inline-block;
		width:1.75rem;
	}
	.red{
		font-size:.26rem;
		color:#ff5555;
		
	} 
	.mt25{
		margin-top:.25rem;
	}
	.mt40{
		margin-top:.4rem;
	}
	.c3{
		font-size:.26rem;
		color:#333;
		line-height:.35rem;
		line-height:.55rem;
	} 
</style>



