<template>
	<div class="invest">
		<app-subHeader :subTitle="subTitle"></app-subHeader>
		<div class="container">  
		 	<p class="conText">  </p>

		 	<p class="c3 mt25"></p>
		 	<p class="conDesc"><img class="flag" src="./img/usa.png" alt="">1.00 = <img class="flag" src="./img/china.png" alt="">  {{im}}</p>
            <p class="c3 mt25"></p>
            <p class="conDesc"><img class="flag" src="./img/usa.png" alt="">1.00 = <img class="flag" src="./img/china.png" alt="">  {{ex}}</p>

		 	<p class="conDesc mt25"> :</p>
			<p class="conDesc"> </p> 
			
			<p class="c3 mt25">	</p> 
			<p class="conDesc">1<br>
				2<br>
				3<br>
				4 : <br> </p> 
		</div>
	</div> 
</template>

<script> 
import subHeader from '../header/SubHeader'  
export default {
	name: 'invest', 
	data () {
	    return {
	    	ex:0,
	    	im:0,
	    	subTitle:'',
	    	 
	    }
  	},
  	created(){
  		this.$http.get("/Mobile/Index/huiLv.html")
  		.then((res)=>{
  			this.im = res.data[0].usdtormb;
  			this.ex = res.data[0].today;
  		})
  	},
  	components:{ 
  		"app-subHeader":subHeader,
  	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>  
	.container{
		padding:.3rem;
	}
	.flag{
		width:.42rem;
		height:.28rem;
		vertical-align:middle;
		margin-right:.15rem;
	}
	.w1{
		display:inline-block;
		width:1rem;
	}
	.w175{
		display:inline-block;
		width:1.75rem;
	}
	.red{
		font-size:.26rem;
		color:#ff5555;
		
	} 
	.mt25{
		margin-top:.25rem;
	}
	.mt40{
		margin-top:.4rem;
	}
	.c3{
		font-size:.3rem;
		color:#333;
		line-height:.35rem;
		line-height:.55rem;
	} 
</style>



