<template>
	<section class="trade">
		<div class="titleBox">
			<h2 class="topTitle"> <i></i>  <i></i> </h2>
			<p class="sub">10/</p>
		</div>
		<div class="imageText flex">
			<div class="items">
				<a href="https://m.golday9999.com/Mobile/Index/real_accountt.html">
			 	<img class="pic" src="./img/trade1.png">
			 	<p class="p1"></p> 
				<p class="p2"><br> </p>  
				</a>
			</div>
			<div class="items">
				<a href="https://www.golday.hk/Mobile/Activity/planmt.html">
			 	<img class="pic" src="./img/trade2.png">
			 	<p class="p1"></p> 
				<p class="p2"> <br></p>  
				</a>
			</div> 
			<router-link class="items" v-for="(item,index) in adsList" :key="index" :to="item.path">
			 	<img class="pic" :src="item.src" alt="item.tag">
			 	<p class="p1">{{item.tag}}</p> 
				<p class="p2" v-html="item.desc"> </p>  
			</router-link> 
			<div class="items">
				<a href="https://vip.golday.hk/Mobile/Index/login.html">
			 	<img class="pic" src="./img/trade4.png">
			 	<p class="p1"></p> 
				<p class="p2"> <br> </p>  
				</a> 
			</div> 
		</div>  
	</section> 
</template>

<script> 
import trade3 from './img/trade3.png'
import trade4 from './img/trade4.png'
import trade5 from './img/trade5.png' 
export default {
	name: 'trade',
  	data () {
	    return {
	    	adsList:[ 
	    		{
	    			tag:"",
	    			desc:"<br>  ",
	    			src:trade3,
	    			path:'/trade/classify'
	    		},
	    		{
	    			tag:"",
	    			desc:" <br>",
	    			src:trade4,
	    			path:'/trade/account'
	    		},
	    		{
	    			tag:"",
	    			desc:" <br> 38/",
	    			src:trade5,
	    			path:'/trade/rules'
	    		} 
	    	]
	    }
  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped> 
	.titleBox{
		padding:0 .3rem .3rem;
		background:#fff;
	} 
	
	.sub{
		font-size: 0.26rem;   
		text-align: center; 
	}
	.flex{
		background:#fff;
		margin-top:.2rem;
		padding:.45rem .3rem;
	}
	.ads{
		width: 3.3rem;
		height: 3.98rem;
		background: #fff;
		border-radius: 0.03rem;
		border: solid 0.02rem #e9e8e8;
		padding-top:.45rem;
		text-align: center;
		margin-bottom:.2rem;
		font-size:0;
	}
	.img{
		width: 1.24rem;
		height: 1.24rem;
	}
	.p1{
		font-size: 0.32rem; 
		color: #323333;
		margin:.2rem 0 .1rem;
	}
	.p2{
		font-size: 0.28rem;  
		line-height:.4rem;
	}
</style>



